// Search

import countries from "../../../data/countries";

const input = document.querySelector("#search-field");
const list = document.querySelector("#search-list");

input.addEventListener("keyup", complete);
list.addEventListener("click", selectWord);

function complete() {
    const val = input.value.trim().toLocaleLowerCase();
    list.style.display = "block";
    if(val.length > 1) {
        const country = countries.filter(county => county.name.toLocaleLowerCase().indexOf(val) === 0);

        if(country.length >= 1) {
            list.innerHTML = renderHtml(country);
        } else {
            list.innerHTML = `<li class="form-select__item"><span>No results...</span></li>`;
        }
    } else {
        list.innerHTML = `<li class="form-select__item"><span>The list is too short...</span></li>`;
    }
}

function selectWord(e) {
    e.preventDefault();
    const target = e.target;
    if(target.parentNode.nodeName === "LI") {
        input.value = target.innerHTML;
        list.style.display = "none";
    }
}

function renderHtml(arr) {
    let html = "";
    for (let i = 0; i < arr.length; i++) {
        html += `<li class="form-select__item"><a href="#">${arr[i].name}</a></li>`
    }

    return html;
}

$(document).on('click', function (e) {
    if(!$(e.target).closest(".search").length) {
        $("#search-list").css("display", "none");
    }
    e.stopPropagation();
});